d;font-size:0.9rem}@media (max-width: 425px){.main .inner-banner .baneer-headline .start-btn{width:90%}}@media (max-width: 768px){.main .inner-banner .baneer-headline .start-btn{width:90%}}.main .inner-banner .img-hero{flex:1 1 45rem;width:100%;height:100%;margin-top:90px}@media (max-width: 425px){.main .inner-banner .img-hero{display:none}}@media (max-width: 768px){.main .inner-banner .img-hero{flex:1 1 45rem}}.main .inner-banner .img-hero img{width:100%;height:100%}.case{width:100%;height:50vh;height:calc(1vh * 50);height:calc(var(--vh, 1vh) * 50);background:#000}.case .card{font-family:myriad-pro-condensed, sans-serif;font-weight:bold;position:relative;width:70%;height:100%;display:flex;margin:auto}@media (max-width: 425px){.case .card{width:100%}}@media (max-width: 768px){.case .card{width:100%}}.case .card .carditem{display:flex;height:100%;width:100%;align-items:center;justify-content:space-between;margin-top:50px}@media (max-width: 425px){.case .card .carditem{flex-direction:column}}.case .card .carditem .case-id{width:100%;height:100%;background:#1b1b1b;color:#fff;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-around}.case .card .carditem .case-id:first-child{background:#CB1C45}.case .card .carditem .case-id:nth-child(2){margin:0 10px}@media (max-width: 425px){.case .card .carditem .case-id:nth-child(2){margin:20px 0}}.case .card .carditem .case-id img{padding:0;margin-left:20px}@media (max-width: 425px){.case .card .carditem .case-id img{margin-top:10px}}.case .card .carditem .case-id .head{margin-left:20px;font-size:1.5rem;font-weight:bold;padding:0}@media (max-width: 425px){.case .card .carditem .case-id .head{font-size:1rem}}.case .card .carditem .case-id .parg{margin-left:20px;margin-right:10px}@media (max-width: 425px){.case .card .carditem .case-id .parg{font-size:0.8rem;margin-top:20px;margin-bottom:20px}}*{box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubun